import React, {useState} from 'react';
import { Container,Row, Col, Modal } from 'react-bootstrap';
import awscert from './assets/aws.png';
import nodecert from './assets/mern.jpg';
import smu from './assets/smu.PNG'
import './cert.css'


function Certification() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Container className="test" >
         <Row>
           <Col>
           <div className="card card-rounded" onClick={handleShow}>
              <div class="embed-responsive embed-responsive-16by9">
                <img alt="Card image cap" class="card-img-top embed-responsive-item" src={awscert} />
              </div>
            </div>
           </Col>
           <Col>
           <div className="card card-rounded" onClick={handleShow}>
              <div className="embed-responsive embed-responsive-16by9">
                <img alt="Card image cap" class="card-img-top embed-responsive-item" src={nodecert} />
              </div>
            </div>
           </Col>
         </Row>
       </Container>

       <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton><image src={smu}/></Modal.Header>
      </Modal>
    </>
  );
}
  
export default Certification