import React from 'react';
import { Button } from 'react-bootstrap';
import './CardPage.css'


function CardPage (props) {
    console.log(props)

    return(
        <div class="container-fluid">
            <div class="row">
                <div class="col-12 mt-4">
                    <div class="card">
                        <div class="card-header">
                            <h5>{props.title}</h5>
                        </div>
                        <div class="card-horizontal">
                            <div class="img-square-wrapper">
                                <img class="" src={props.image} responsive style={{ width: 200}} resizeMode="contain" alt="Card image cap"/>
                            </div>
                            <div class="card-body">
                                <h6 class="card-title">{props.author}</h6>
                                <p class="card-text">{props.abstract}</p>
                            </div>
                        </div>
                        <Button variant="info" href={props.url}>View</Button>
                    </div>
                </div>
            </div>
        </div>
    );

}

export default CardPage;

