import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import './home.css';
import Emoji from './Emoji';
import ProfileCard from './ProfileCard'

function Portfolio(){
    return(
        <Container className="portfolio">
            <Row>
                <Col>
                    <Row>
                        <h4>Hi'll {<Emoji symbol="👋"/>} !!!</h4>
                    </Row>
                    <Row>
                        <p>
                            Hi, My name is Prabin Singh. I am from Keller, TX and currently enrolled in Southern Methodist University as Full Stack Web Developer student.
                            This is my first website and I will be adding different projects in portfolio section of this page as I make progress in my Class. 
                            So far this page contains three different application which has been developed using vanilla Js, jQuery for backend and HTML for front end. 
                            Styling has been embedded using css whereas there are few different frameworks used to give some effects and animation when page loads.
                        </p>
                    </Row>
                </Col>
                <Col md={{ span: 5}} className="profileCard">
                    <ProfileCard />
                </Col>
            </Row>
        </Container>
    );
}

export default Portfolio;